.navbar .logo {
  color: #fff;
  letter-spacing: 1px;
  font-family: Times New Roman, Times, serif;
  font-size: 2rem;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s, transform .3s;
  position: relative;
}

.navbar .logo:hover {
  color: gold;
