  border: none;
  font-size: 2rem;
  position: relative;
}

.hamburger-button:before, .hamburger-button:after, .hamburger-button div {
  content: "";
  transition: all .3s all;
  background-color: #fff;
  width: 30px;
  height: 3px;
  margin: 5px auto;
  display: block;
}

.hamburger-button:hover:before {
  transform: translateY(8px)rotate(45deg);
}

.hamburger-button:hover:after {
  transform: translateY(-8px)rotate(-45deg);
}

.hamburger-button:hover div {
  opacity: 0;
}

